/* eslint-disable @typescript-eslint/ban-types */
import type { FC } from "react";
import { forwardRef, memo } from "react";
import { makeStyles } from "./lib/ThemeProvider";
import type { IconProps } from "./Icon";
import { id } from "tsafe/id";
import { useGuaranteedMemo } from "powerhooks/useGuaranteedMemo";
import MuiButton from "@mui/material/Button";
import { capitalize } from "tsafe/capitalize";
import { assert } from "tsafe";
import type { Equals } from "tsafe/Equals";
import { breakpointsValues } from "./lib";
import { variantNameUsedForMuiButton } from "./lib/typography";
import { pxToNumber } from "./tools/pxToNumber";

export type ButtonProps<IconId extends string = never> =
    | ButtonProps.Regular<IconId>
    | ButtonProps.Submit<IconId>;

export namespace ButtonProps {
    type Common<IconId extends string> = {
        className?: string;

        /** Defaults to "primary" */
        variant?: "primary" | "secondary" | "ternary";

        children: React.ReactNode;

        /** Defaults to false */
        disabled?: boolean;

        startIcon?: IconId;
        endIcon?: IconId;

        /** Defaults to false */
        autoFocus?: boolean;

        tabIndex?: number;

        name?: string;
        htmlId?: string;
        "aria-label"?: string;
    };

    export type Regular<IconId extends string = never> = Common<IconId> & {
        onClick?: (e: React.MouseEvent<HTMLButtonElement, MouseEvent>) => void;
        href?: string;
        /** Default to true if href */
        doOpenNewTabIfHref?: boolean;
    };

    export type Submit<IconId extends string = never> = Common<IconId> & {
        type: "submit";
    };
}

export function createButton<IconId extends string = never>(params?: {
    Icon(props: IconProps<IconId>): ReturnType<FC>;
}) {
    const { Icon } = params ?? {
        "Icon": id<(props: IconProps<IconId>) => JSX.Element>(() => <></>),
    };

    const Button = memo(
        forwardRef<HTMLButtonElement, ButtonProps<IconId>>((props, ref) => {
            const {
                className,
                variant = "primary",
                disabled = false,
                children,
                startIcon,
                endIcon,
                autoFocus = false,
                tabIndex,
                name,
                htmlId,
                "aria-label": ariaLabel,
                //For the forwarding, rest should be empty (typewise)
                ...rest
            } = props;

            const { classes, cx } = useStyles({
                variant,
                disabled,
            });

            const IconWd = useGuaranteedMemo(
                () => (props: { iconId: IconId }) =>
                    (
                        <Icon
                            iconId={props.iconId}
                            className={classes.icon}
                            size="default"
                        />
                    ),
                [disabled, classes.icon],
            );

            return (
                <MuiButton
                    disableFocusRipple
                    ref={ref}
                    className={cx(classes.root, className)}
                    //There is an error in @mui/material types, this should be correct.
                    disabled={disabled}
                    startIcon={
                        startIcon === undefined ? undefined : (
                            <IconWd iconId={startIcon} />
                        )
                    }
                    endIcon={
                        endIcon === undefined ? undefined : (
                            <IconWd iconId={endIcon} />
                        )
                    }
                    autoFocus={autoFocus}
                    tabIndex={tabIndex}
                    name={name}
                    id={htmlId}
                    aria-label={ariaLabel}
                    {...(() => {
                        if ("type" in rest) {
                            const { type, ...restRest } = rest;

                            //For the forwarding, rest should be empty (typewise),
                            assert<Equals<typeof restRest, {}>>();

                            return {
                                type,
                                ...restRest,
                            };
                        }

                        const {
                            onClick,
                            href,
                            doOpenNewTabIfHref = href !== undefined,
                            ...restRest
                        } = rest;

                        return {
                            onClick,
                            href,
                            "target": doOpenNewTabIfHref ? "_blank" : undefined,
                            ...restRest,
                        };
                    })()}
                >
                    {typeof children === "string"
                        ? capitalize(children)
                        : children}
                </MuiButton>
            );
        }),
    );

    const useStyles = makeStyles<{
        variant: NonNullable<ButtonProps["variant"]>;
        disabled: boolean;
    }>({ "name": { Button } })((theme, { variant, disabled }) => {
        const textColor =
            theme.colors.useCases.typography[
                disabled
                    ? "textDisabled"
                    : (() => {
                          switch (variant) {
                              case "primary":
                                  return "textFocus";
                              case "secondary":
                              case "ternary":
                                  return "textPrimary";
                          }
                      })()
            ];

        const hoverTextColor = (() => {
            switch (theme.isDarkModeEnabled) {
                case true:
                    switch (variant) {
                        case "ternary":
                            return theme.colors.useCases.surfaces.background;
                        default:
                            return theme.colors.palette.dark.main;
                    }
                case false:
                    switch (variant) {
                        case "primary":
                            return theme.colors.palette.dark.main;
                        case "secondary":
                            return theme.colors.palette.light.main;
                        case "ternary":
                            return theme.colors.useCases.surfaces.background;
                    }
            }
        })();

        return {
            "root": (() => {
                const hoverBackgroundColor =
                    theme.colors.useCases.buttons[
                        (() => {
                            switch (variant) {
                                case "primary":
                                    return "actionHoverPrimary";
                                case "secondary":
                                case "ternary":
                                    return "actionHoverSecondary";
                            }
                        })()
                    ];

                const paddingSpacingTopBottom = 2;

                const borderWidth = (() => {
                    switch (variant) {
                        case "primary":
                        case "secondary":
                            return 2;
                        case "ternary":
                            return 0;
                    }
                })();

                const approxHeight =
                    2 * theme.spacing(paddingSpacingTopBottom) +
                    2 * borderWidth +
                    pxToNumber(
                        theme.typography.variants[variantNameUsedForMuiButton]
                            .style.lineHeight,
                    );

                return {
                    "textTransform": "unset" as const,
                    "backgroundColor": disabled
                        ? theme.colors.useCases.buttons.actionDisabledBackground
                        : (() => {
                              switch (variant) {
                                  case "primary":
                                  case "secondary":
                                      return "transparent";
                                  case "ternary":
                                      return theme.colors.useCases.surfaces
                                          .background;
                              }
                          })(),
                    "borderRadius": approxHeight / 2,
                    borderWidth,
                    "borderStyle": "solid",
                    "borderColor": disabled
                        ? "transparent"
                        : hoverBackgroundColor,
                    ...theme.spacing.topBottom(
                        "padding",
                        paddingSpacingTopBottom,
                    ),
                    ...theme.spacing.rightLeft(
                        "padding",
                        (() => {
                            if (
                                theme.windowInnerWidth >= breakpointsValues.xl
                            ) {
                                return 3;
                            }

                            return 4;
                        })(),
                    ),
                    "&.MuiButton-text": {
                        "color": textColor,
                    },

                    "&:hover, &:focus": {
                        "backgroundColor": hoverBackgroundColor,
                        "& .MuiSvgIcon-root": {
                            "color": hoverTextColor,
                        },
                        "&.MuiButton-text": {
                            "color": hoverTextColor,
                        },
                    },
                } as const;
            })(),
            "icon": {
                "color": textColor,
            },
        };
    });

    return { Button };
}
